import axios from 'axios'
import { Notification } from 'element-ui'
import $store from '@/store'
import { getLoginPath } from '@/utils/auth'

// create an axios instance
const baseConfig = {
  baseURL: process.env.VUE_APP_BASE_API_WMS, // apibase_url
  withCredentials: true,
  timeout: 30000, // request timeout
  headers: {
    'Content-Type': 'application/json',
    Accept: 'application/json'
  }
}

const service = axios.create(baseConfig)

let ToskTap = false

// request interceptor
service.interceptors.request.use(
  config => {
    // console.log('request', $store.getters.language)
    // Do something before request is sent
    if ($store.getters.token) {
      // token
      config['responseType'] = 'blob'
      config.headers['Authorization'] = `${$store.getters.token}`
      // 
      // config.headers['Accept-Language'] = `${$store.getters.language === 'en' ? 'en-US' : 'zh-CN'}`
    }
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// respone interceptor
service.interceptors.response.use(
  response => {
    if (response && response.data) {
      if (response.data.code && response.data.code === 999) {
        /*
        *   
        */
        Notification.error({
          message: response.data.msg,
          duration: 5 * 1000
        })
      }
    }
    return response
  },
  error => {
    console.log('err' + error) // for debug
    let code = 0
    try {
      code = error.response.status
    } catch (e) {
      if (error.toString().indexOf('Error: timeout') !== -1) {
        Notification.error({
          title: '',
          duration: 5000
        })
        return Promise.reject(error)
      }
    }
    /*
    *   token
    */
    if (code === 401 || code === 403) {
      if (!ToskTap) {
        ToskTap = true
        Notification.error({
          title: '',
          duration: 5000
        })
        setTimeout(() => {
          ToskTap = false
        }, 5000)
      }
      // 
      $store.dispatch('resetToken').then(() => {
        setTimeout(() => {
          window.location.href = getLoginPath()
        }, 3 * 1000)
      })
      return Promise.reject(error)
    } else {
      Notification.error({
        title: error,
        duration: 5000
      })
      return Promise.reject(error)
    }
  }
)

export default service
