<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >

    <el-scrollbar class="scroll-dialog">
      <div class="clearfix top-filter">
        <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
          <el-form-item prop="materialCodeOrName" class="fl">
            <el-input v-model="query.materialCodeOrName" filterable clearable placeholder="" style="width: 178px;" class="filter-item" />
          </el-form-item>
          <el-button
            class="filter-item"
            type="success"
            icon="el-icon-search"
            @click="searchInit"
          ></el-button>
          <!-- <el-button
              class="filter-item"
              type="warning"
              icon="el-icon-refresh-left"
              @click="resetForm('filterItem')"
            ></el-button> -->
        </el-form>
      </div>
      <!--  -->
      <el-row>
        <table-com
          :ref="refName"
          :table-height-type="490"
          :loading="loading"
          :index="false"
          :index-fixed="false"
          :selection="true"
          :multiple="multiple"
          :selection-fixed="false"
          :table-column-sort="false"
          :table-data="tableData"
          :column-list="columnList"
          :show-operate="false"
          :operate-fixed="false"
          :table-selection="tableSelection"
          @selectionChange="selectionChange"
        />
      </el-row>
      <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="confirmDialog"> </el-button>
      <el-button @click="beforeClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
import paginationPage from '@/mixins/paginationPage'
import { getListApi as getListSelectApi } from '@/apis/modules/base/baseMaterialInformation'

export default {
  name: 'MaterialSelect',
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    // id  {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // ids
    selection: {
      type: Array,
      default: () => []
    },
    // 
    max: {
      type: Number,
      default: 0
    },
    ids: {
      type: Array,
      default: () => []
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      refName: 'modelSelectMaterialSelect',
      query: {
        materialCodeOrName: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'code',
          label: '',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'name',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'spec',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'assessType_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'description',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'type_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'minOutboundUnitName',
          label: 'SKU',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'isExpirationDate',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (c) => c.isExpirationDate === 0 ? '' : ''
        },
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupDescription',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'dataSources_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'number',
          prop: 'deliveryNumber',
          label: '',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => {
            return 0
          },
          setPrecision: (row) => {
            return 3
          }
          // setMax: (row) => {
          //   return row.orderNum
          // }
        }
      ],
      columnSets: [],
      tableSelection: [],
      deptSelectedInfo: {
        list: [],
        visible: false,
        radio: ''
      }
    }
  },
  methods: {
    confirmDialog() {
      this.$emit('confirm', this.tableSelection)
      this.beforeClose()
    },
    init() {
      const params = {
        ...this.query,
        materialIdList: this.ids,
        ...this.extraInfo,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListSelectApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list.map(i => {
              return {
                uuid: uuidv4(),
                deliveryNumber: 0,
                ...i
              }
            })
            // selection
            this.tableSelection = this.tableData.filter(i => this.selection.includes(i.id))
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initSelect() {
      this.init()
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 
    handleUpdateTableSelected(type = '') {

    },
    handleDefault() {
      this.tableSelection = new Array(0)
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.handleDefault()
      typeof done === 'function' && done()
    },
    onDeptSelectConfirm(list) {
      if (list.length) {
        this.query.usedOffice = list[0].name
      }
    },
    // Tip 
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
