<template>
  <div class="app-container">
    <el-row>
      <table-com
        ref="baseTableRef"
        :table-height-type="485"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="false"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      />
    </el-row>

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getPurchaseOrderHistoryApi as getListApi } from '@/apis/modules/in-storage/purchase'

export default {
  name: 'InStorageGrounding',
  mixins: [paginationPage],
  data() {
    return {
      query: {},
      type: 4,
      title: this.$route.meta.title,
      loading: false,
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'deliveryReceiptNo',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'deliveryReceiptMaterialItems',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'deliveryReceiptMaterialNum',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inStorageItems',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inStorageNum',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inStorageAmount',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inStorageVoucher',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'returnMaterialsItems',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'returnMaterialsNum',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
        // {
        //   module: 'word',
        //   prop: 'statusStr',
        //   label: '',
        //   align: 'center',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // }
      ]
    }
  },
  watch: {
    '$attrs.node.wmsId': {
      handler(value) {
        if (value) {
          this.init()
        }
      },
      immediate: true
    }
  },
  methods: {
    init() {
      this.loading = true
      const params = {
        ...this.query,
        // type: this.type,
        orderId: this.$attrs.node.wmsId
        // materialIdList: []
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '') {
        return false
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>

