/*
*   
*/
export const noCacheTreeMixins = {
  data() {
    return {
      treeRefName: 'tree',
      defaultPropsMixins: {
        children: 'children',
        label: 'name',
        isLeaf: (data) => {
          return !data.hasleaf
        }
      },
      treeLoadingMixins: false,
      // 
      treeQueryFocus: false, // 
      treeQueryMixins: '',
      // 
      treeDataMixins: [],
      defaultCheckedNodeMixins: '', // id
      defaultExpandKeysMixins: [] // id
    }
  },
  methods: {
    getCurrentTreeStatus() {
      this.defaultCheckedNodeMixins = this.$refs[this.treeRefName].getCurrentKey()
    },
    handleRefreshTree() {
      this.defaultCheckedNodeMixins = this.$refs[this.treeRefName].getCurrentKey()
      const node = this.nodeInfo
      this.initTreeMixins().then(() => {
        this.setCurrentNode(node)
      })
    },
    /*
    *  
    */
    handleNodeExpand(data) {
      let flag = false
      this.defaultExpandKeysMixins.some(item => {
        if (item === data.id) {
          flag = true
          return true
        }
      })
      if (!flag) {
        this.defaultExpandKeysMixins.push(data.id)
      }
    },
    /**
     * 
     * @param {*} data 
     */
    handleNodeCollapse(data) {
      this.defaultExpandKeysMixins.forEach((item, index) => {
        if (item === data.id) {
          this.defaultExpandKeysMixins.splice(index, 1)
        }
      })
    },
    /*
    * 1.  $attrs.node 
    */
    resetCurrentNode() {
      console.log('')
      this.nodeInfo = {}
      this.initTreeMixins().then(() => {
        this.$nextTick(() => {
          this.$refs[this.treeRefName].setCurrentKey()
        })
      })
    },
    /*
    * 2. 
    */
    setCurrentNode(data) {
      console.log('', data)
      // 1. 
      this.$nextTick(() => {
        this.$refs[this.treeRefName].setCurrentKey()
        // 2. 
        this.$nextTick(() => {
          this.$refs[this.treeRefName].setCurrentKey(data.id)
          // 
          const node = this.$refs[this.treeRefName].getNode(data.id)
          this.handleNodeClick(node.data, node)
        })
      })
    }
  }
}
