<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    :visible="value"
    size="70%"
    :destroy-on-close="true"
    direction="btt"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
      <el-form-item label="" prop="code">
        <el-input v-model.trim="form.code" :disabled="editType !== 'add'" placeholder="" style="width: 178px;" />
      </el-form-item>
      <el-form-item label="" prop="supplierId">
        <el-input v-if="editType === 'add'" v-model="form.supplierName" placeholder="" style="width: 178px;" disabled />
        <div v-if="editType === 'modify'" class="selected-wrapper">
          <el-input v-model.trim="form.supplierName" disabled placeholder="" style="width: 143px;" />
          <el-button type="primary" style="margin-left:5px;padding-left:0;padding-right:0;width:30px" icon="el-icon-search" @click="selectInfo.visible = true" />
        </div>
      </el-form-item>
      <el-form-item label="" prop="purchaseDate">
        <el-date-picker
          v-model="form.purchaseDate"
          type="date"
          style="width: 178px;"
          value-format="yyyy-MM-dd"
          placeholder=""
          :clearable="false"
        />
      </el-form-item>
      <!-- <el-form-item label="" prop="orderAmount">
          <el-input-number v-model="form.orderAmount" placeholder="" :min="0" style="width: 178px;" />
        </el-form-item> -->
      <el-form-item label="" prop="materialsUnit">
        <!-- <el-input-number v-model="form.materialsUnit" placeholder="" :min="0" style="width: 178px;" /> -->
        <el-input v-model.trim="form.materialsUnit" placeholder="" style="width: 178px;" />
      </el-form-item>
      <!-- <el-form-item label="" prop="type">
          <el-input-number v-model="form.type" placeholder="" :min="0" style="width: 178px;" />
        </el-form-item> -->
      <el-form-item label="" prop="contact">
        <el-input v-model.trim="form.contact" placeholder="" style="width: 178px;" />
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model.trim="form.phone" placeholder="" style="width: 178px;" />
      </el-form-item>
      <!-- <el-form-item label="" prop="dataSources">
          <el-input v-model.trim="form.dataSources" placeholder="" style="width: 178px;" />
        </el-form-item> -->

      <el-row style="padding: 0 10px;">
        <divider></divider>
        <el-row style="margin: 10px 0;">
          <el-button type="primary" icon="el-icon-plus" @click="materielSelectInfo.visible = true"></el-button>
        </el-row>
        <table-com
          :key="refreshKey"
          ref="baseTableRef"
          :loading="loading"
          :index="true"
          :selection="false"
          :table-data="tableData"
          :column-list="columnList"
          :table-height-type="300"
          :operation-width="100"
          :expand-list="expandList"
          :table-column-sort="false"
          @rowClick="handleRowClick"
          @refresh="searchInit"
        >
          <template slot-scope="scope">
            <el-button type="danger" @click="handleDelete(scope.row)"></el-button>
          </template>
        </table-com>
      </el-row>
      <!-- <el-form-item label="" prop="supplierId">
          <template>
            <div class="selected-wrapper">
              <el-input v-model.trim="form.materialShow" disabled placeholder="" style="width: 143px;" />
              <el-button plain type="primary" style="margin-left:5px;padding-left:0;padding-right:0;width:30px" icon="el-icon-search" @click="selectInfo.visible = true" />
            </div>
          </template>
        </el-form-item> -->
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? ' ...' : ' ' }}</el-button>
        <el-button @click="beforeClose"> </el-button>
      </div>
    </el-form>

    <!--   -->
    <model-select
      v-model="selectInfo.visible"
      :multiple="false"
      @confirm="onSelectConfirm"
    />

    <!--    -->
    <model-select-materiel
      v-model="materielSelectInfo.visible"
      :ids="tableData.map(i => i.materialId)"
      @confirm="onMaterielSelectConfirm"
    />

  </el-drawer>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
import _ from 'lodash'
import paginationPage from '@/mixins/paginationPage'
import ModelSelectMateriel from '@/components/ModalSelect/Material'
import ModelSelect from '@/components/ModalSelect/Supplier'
import { addOrderApi as addApi, editOrderApi as updateApi, delOrderMaterialApi } from '@/apis/modules/in-storage/purchase'

const INIT_FORM = () => ({
  purchaseDate: '',
  materialsUnit: '',
  // orderAmount: 0,
  phone: '',
  contact: '',
  code: '',
  supplierId: '',
  supplierName: '',
  //  0Wms1SAP
  dataSources: 0,
  materialShow: '',
  wmsOrderMaterialJoinList: []
})

export default {
  name: 'InStoragePurchaseTreeAddCom',
  components: {
    ModelSelect,
    ModelSelectMateriel
  },
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return INIT_FORM()
      }
    },
    node: {
      type: Object,
      default: () => {}
    },
    orderType: {
      type: [Number, String], // 1234
      default: 1
    }
  },
  data() {
    return {
      refreshKey: new Date().getTime(),
      form: INIT_FORM(),
      loading: false,
      rules: {
        code: [
          { required: true, message: '', trigger: 'blur' }
        ],
        supplierId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        purchaseDate: [
          { required: true, message: '', trigger: 'blur' }
        ],
        materialsUnit: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' }
        ],
        contact: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      query: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'assessType_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitMaterial',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'number',
          prop: 'orderNum',
          label: '',
          align: 'center',
          minWidth: 120,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => {
            return 0
          },
          setPrecision: (row) => {
            return 3
          }
        },
        {
          module: 'number',
          prop: 'price',
          label: '',
          align: 'center',
          minWidth: 120,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => {
            return 0
          },
          setPrecision: (row) => {
            return 3
          }
        }
      ],
      expandList: [],
      selectInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      materielSelectInfo: {
        list: [],
        visible: false,
        radio: ''
      }
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        this.refreshKey = new Date().getTime()
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM(), supplierId: this.node.supplierId, supplierName: this.node.supplierName }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = false
          if (this.editType === 'add') {
            addApi({
              ...this.form,
              wmsOrderMaterialJoinList: this.tableData,
              contractId: this.node.wmsId,
              type: this.orderType // 1234
            }).then(res => {
              const { code } = res
              if (!code) {
                this.$message({
                  message: `${this.title}`,
                  type: 'success'
                })
                this.$listeners.handleRefreshTree(false, false)
                this.beforeClose()
              }
              this.loading = false
            }).catch(err => {
              console.log(err)
              this.loading = false
            })
          } else {
            updateApi({
              ...this.form,
              wmsOrderMaterialJoinList: this.tableData
            }).then(res => {
              const { code } = res
              if (!code) {
                this.$message({
                  message: `${this.title}`,
                  type: 'success'
                })
                this.$listeners.handleRefreshTree(false, true)
                this.beforeClose()
              }
              this.loading = false
            }).catch(err => {
              console.log(err)
              this.loading = false
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    onSelectConfirm(list) {
      if (list.length) {
        this.form.supplierId = list[0].id
        this.form.supplierName = list[0].name
      }
    },
    // 
    onMaterielSelectConfirm(list) {
      console.log('!!!!!', list)
      if (list.length) {
        const orderId = this.form.id || null
        const selectList = list.map(i => {
          return {
            ...i,
            uuid: uuidv4(),
            orderId,
            orderMaterialId: i.id,
            orderNum: 0,
            price: 0,
            materialId: i.id,
            materialName: i.name,
            materialCode: i.code,
            materialSpec: i.spec,
            unitMaterial: i.unitName
          }
        })
        console.log('!!!!!', selectList)
        this.tableData = [...this.tableData, ...selectList]
      }
    },
    // 
    handleDelete(item) {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        delOrderMaterialApi({ id: item.id }).then(res => {
          const { code } = res
          if (!code) {
            this.tableData = this.tableData.filter(i => i.uuid !== item.uuid)
            this.$notify.success({
              title: '',
              message: ``
            })
          }
        }).catch(err => {
          console.log('err', err)
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
    },
    initSelect() {
      if (this.editType !== 'add' && this.dialogForm.wmsOrderMaterialJoinList.length) {
        this.tableData = _.cloneDeep(this.dialogForm.wmsOrderMaterialJoinList).map(i => {
          return {
            uuid: uuidv4(),
            ...i
          }
        })
      }
    },
    beforeClose(done) {
      console.log('')
      this.form = INIT_FORM()
      this.tableData = new Array(0)
      this.$refs.form.resetFields()
      // this.form.wmsOrderMaterialJoinList = new Array(0)
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep:focus {
    outline: 0;
  }
  .scroll-drawer {
    // height: calc(100vh - 150px);
    // overflow: auto;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  .drawer-footer{
    text-align: right;
  }
  </style>

