<template>
  <div class="app-container">
    <el-row>
      <table-com
        :ref="refTableName"
        :table-height-type="445"
        :ref-name="refTableName"
        :loading="loading"
        :index="true"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="130"
        :expand-list="expandList"
        :refresh-key="refreshKey"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          {{ scope.row ? '---' : '' }}
        </template>
      </table-com>
    </el-row>

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import tableHeadSet from '@/mixins/tableHeadSet'
import { allMaterialListApi as getListApi } from '@/apis/modules/in-storage/purchase'

export default {
  name: 'InStorageGrounding',
  mixins: [paginationPage, tableHeadSet],
  data() {
    return {
      type: 1,
      title: this.$route.meta.title,
      loading: false,
      query: {},
      refTableName: 'baseTableRef',
      columnSets: [],
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `${this.title}`
      } else {
        return `${this.title}`
      }
    }
  },
  watch: {
    '$attrs.node.wmsId': {
      handler(value) {
        if (value) {
          this.init()
        }
      },
      immediate: true
    }
  },
  mounted() {
    // this.init()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type
        ? {
          ...this.query,
          id: this.$attrs.node.wmsId,
          nameOrCode: this.$attrs.nameOrCode
          // type: this.type,
          // materialIdList: []
        }
        : {
          ...this.query,
          id: this.$attrs.node.wmsId,
          nameOrCode: this.$attrs.nameOrCode
          // type: this.type,
          // materialIdList: []
        }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            // eslint-disable-next-line no-unused-vars
            const { formIdent, formHeadSets, pageData, pageList } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            // const { list, ...pageInfo } = pageData
            // this.initPage(pageInfo)
            this.tableData = pageList
            this.$emit('getMaterialNum', this.tableData.length)
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '') {
        return false
      }
    },
    refresh() {
      // 
      this.init('clean')
    },
    // 
    handleDelete(params, item) {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        console.log({
          id: params[0],
          dataSources: item.dataSources
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      // this.dialogForm = { ...item }
      // this.editType = 'modify'
      // this.dialogVisible = true
      // this.$router.push({
      //   name: 'InStorageInfoDetail',
      //   params: {
      //     id: item.id
      //   }
      // })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
