<template>
  <div class="app-container">
    <el-descriptions :column="1">
      <el-descriptions-item label="">{{ totalAmount }}</el-descriptions-item>
    </el-descriptions>
    <el-row>
      <table-com
        ref="baseTableRef"
        :table-height-type="400"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="false"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      />
    </el-row>

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getListNewApi } from '@/apis/modules/in-storage/purchase'

export default {
  name: 'InStorageGrounding',
  mixins: [paginationPage],
  data() {
    return {
      type: 5,
      title: this.$route.meta.title,
      loading: false,
      query: {

      },
      tableData: [],
      totalAmount: '',
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderNum',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'price',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitMaterial',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inEnterNumber',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `${this.title}`
      } else {
        return `${this.title}`
      }
    }
  },
  watch: {
    '$attrs.node.wmsId': {
      handler(value) {
        if (value) {
          this.init()
        }
      },
      immediate: true
    }
  },
  mounted() {
    // this.init()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type
        ? {
          ...this.query,
          id: this.$attrs.node.wmsId,
          nameOrCode: this.$attrs.nameOrCode,
          type: this.type,
          materialIdList: []
        }
        : {
          ...this.query,
          type: this.type,
          id: this.$attrs.node.wmsId,
          nameOrCode: this.$attrs.nameOrCode,
          materialIdList: []
        }
      return new Promise((resolve, reject) => {
        getListNewApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data.materialList
            this.totalAmount = data.totalAmount
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 
      this.init('clean')
    },
    // 
    handleDelete(params, item) {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        console.log({
          id: params[0],
          dataSources: item.dataSources
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      // this.dialogForm = { ...item }
      // this.editType = 'modify'
      // this.dialogVisible = true
      // this.$router.push({
      //   name: 'InStorageInfoDetail',
      //   params: {
      //     id: item.id
      //   }
      // })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
