<template>
  <div
    v-loading="uploadLoading"
    class="app-container"
    element-loading-text=""
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <!--  -->
    <el-form ref="filterItem" :model="query" label-width="0">
      <el-row type="flex" justify="space-around" :gutter="20">
        <el-col>
          <el-form-item prop="contractName">
            <el-input v-model.trim="query.contractName" clearable placeholder="" class="filter-item" style="width: 100%;" />
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item prop="supplierName">
            <el-input v-model.trim="query.supplierName" clearable placeholder="" class="filter-item" style="width: 100%;" />
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item prop="startDate">
            <el-date-picker
              v-model="query.startDate"
              class="filter-item"
              type="date"
              style="width: 100%;"
              value-format="yyyy-MM-dd"
              placeholder=""
              @change="query.endDate = ''"
            />
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item prop="endDate">
            <el-date-picker
              v-model="query.endDate"
              class="filter-item"
              type="date"
              style="width: 100%;"
              value-format="yyyy-MM-dd"
              placeholder=""
              :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startDate).getTime()}"
            />
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item prop="materialsUnit">
            <el-input v-model.trim="query.materialsUnit" clearable placeholder="" class="filter-item" style="width: 100%;" />
          </el-form-item>
        </el-col>
      </el-row>

    </el-form>
    <el-row class="clearfix">
      <div class="fl">
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="handleSearch"
        ></el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        ></el-button>
      </div>
      <div class="fr">
        <el-button type="primary" @click="handleGoMaterialSupplier"></el-button>
        <el-button style="margin-right: 10px;" type="success" icon="el-icon-download" @click="downloadExcel"></el-button>
        <!-- /  -->
        <el-upload
          ref="upload"
          style="display: inline-block;margin-right: 10px;"
          sizi="mini"
          :action="`${BASE_API}/enterWarehouse/wmsContract/importOrder`"
          :headers="headers"
          accept=".xls,.xlsx"
          :show-file-list="false"
          :on-success="handleUploadSuccess"
          :before-upload="beforeUpload"
        >
          <el-button size="mini" icon="el-icon-upload" type="primary"></el-button>
        </el-upload>
        <!-- <div style="display: inline-block;margin-right: 10px;" /> -->
        <!-- <form-upload-file
            ref="uploadFile"
            btn-content=""
            btn-icon="el-icon-upload"
            url="/enterWarehouse/wmsContract/importOrder"
            :show-file-list="false"
            accept=".xls,.xlsx"
            :limit="9999999999999999"
            style="margin-right: 10px;"
            @uploadFileSuccess="uploadFileSuccess"
          /> -->
        <el-button class="filter-item" type="primary" icon="el-icon-plus" @click="handleAdd"></el-button>
        <!-- ZHY2024 -->
        <el-button class="filter-item" type="warning" icon="el-icon-edit" :disabled="!+nodeInfo.id || nodeInfo.parentId !== 0 || nodeInfo.code === 'ZHY2024'" @click="handleModify"></el-button>
        <el-button class="filter-item" type="danger" icon="el-icon-delete" :disabled="!+nodeInfo.id || nodeInfo.parentId !== 0 || nodeInfo.code === 'ZHY2024'" @click="handleDelete"></el-button>
      </div>
    </el-row>
    <!--  -->
    <drag-screen :extra-minus-height="90" :left-weight="4">
      <template v-slot:left>
        <div>
          <el-row class="connection-line">
            <el-tree
              :ref="treeRefName"
              :key="refreshKey"
              v-loading="treeLoadingMixins"
              :data="treeDataMixins"
              :indent="0"
              :props="defaultPropsMixins"
              highlight-current
              :check-strictly="true"
              node-key="id"
              :lazy="false"
              :expand-on-click-node="false"
              :default-expanded-keys="defaultExpandKeysMixins"
              :current-node-key="defaultCheckedNodeMixins"
              @node-expand="handleNodeExpand"
              @node-collapse="handleNodeCollapse"
              @node-click="handleNodeClick"
            >
              <span slot-scope="{ node, data }" class="custom-tree-node">
                <!--  -->
                <template>
                  <div class="node-content">
                    <!--  -->
                    <span :style="{color: data.allStockIn ? '#0A9607' : '',marginRight: '5px'}">{{ `${data.code}` }}</span>
                    <!--  -->
                    <span v-if="node.level === 1 && data.type == '3'">
                      (<span :style="{color: contractType[data.type].color}">{{ `${contractType[data.type].text}` }}</span>)
                    </span>
                  </div>
                </template>
              </span>
            </el-tree>
          </el-row>
        </div>
      </template>
      <template v-slot:right>
        <right
          ref="rightView"
          :node="nodeInfo"
          @refreshTree="handleRefreshTree"
          @resetCurrentNode="resetCurrentNode"
          @handleRefreshTree="handleRefreshTree"
          @toggleLoading="handleToggleLoading"
        />
      </template>
    </drag-screen>

    <!--  -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      @submit="handleRefreshTree"
      @handleRefreshTree="resetCurrentNode"
    />

  </div>
</template>

<script>
import DragScreen from '@/components/DragScreen'
import Right from './Right'
import EditDialog from './Dialog'
import { noCacheTreeMixins } from './tree.mixin'
import { getTreeListApi, delTreeListApi as getDeleteApi, getContractDetailApi } from '@/apis/modules/in-storage/purchase'
import { formUploadFilesMixins } from '@/mixins/upload'
import $store from '@/store'

export default {
  name: 'InStorageFinish',
  components: {
    DragScreen,
    Right,
    EditDialog
  },
  mixins: [noCacheTreeMixins, formUploadFilesMixins],
  props: {
  },
  data() {
    const token = `${$store.getters.token}`
    return {
      title: this.$route.meta.title,
      loading: false,
      refreshKey: 1,
      query: {
        contractName: '',
        supplierName: '',
        materialsUnit: '',
        startDate: '',
        endDate: ''
      },
      nodeInfo: {},
      dialogForm: {},
      editType: 'add',
      dialogVisible: false,
      BASE_API: process.env.VUE_APP_BASE_API_WMS,
      headers: { 'Authorization': token },
      uploadLoading: false,
      contractType: {
        '1': {
          text: '',
          color: '#2ec7c9'
        },
        '2': {
          text: '',
          color: '#b6a2de'
        },
        '3': {
          text: '',
          color: '#5ab1ef'
        },
        '4': {
          text: '',
          color: '#ffb980'
        }
      }
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return ``
      } else {
        return ``
      }
    }
  },
  watch: {
    defaultExpandKeysMixins(val) {
      console.log('id', val)
    },
    defaultCheckedNodeMixins(val) {
      console.log('', val)
    }
  },
  activated() {
    if (Object.keys(this.$route.params).length) {
      const { contractCode, orderCode } = this.$route.params
      this.initTreeMixins()
        .then(res => {
          // 
          if (contractCode) {
            // Tip 
            const nodeData = this.treeDataMixins.find(item => item.code === contractCode)
            const setNodeData = !nodeData.children.length ? nodeData : nodeData.children[0]
            // Tip ()
            this.defaultExpandKeysMixins = [setNodeData.id]
            this.defaultCheckedNodeMixins = setNodeData.id
            // Tip ()
            this.setCurrentNode({ ...setNodeData })
          }
          if (contractCode && orderCode) {
            // Tip 
            const nodeData = this.treeDataMixins.find(item => item.code === contractCode)
            // Tip 
            const setNodeData = !nodeData.children.length ? nodeData : nodeData.children.find(orderItem => orderItem.code === orderCode)
            // Tip 
            this.defaultExpandKeysMixins = [setNodeData.id]
            this.defaultCheckedNodeMixins = setNodeData.id
            // Tip 
            this.setCurrentNode({ ...setNodeData })
          }
        })
        .catch(err => {
          console.log('err', err)
        })
    }
  },
  mounted() {
    this.initTreeMixins()
  },
  methods: {
    initTreeMixins() {
      return new Promise((resolve, reject) => {
        const params = {
          ...this.query
        }
        this.treeLoadingMixins = true
        getTreeListApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            this.refreshKey++
            this.treeDataMixins = data
          }
          this.treeLoadingMixins = false
          resolve(res)
        }).catch(err => {
          this.treeLoadingMixins = false
          reject(err)
        })
      })
    },
    // 
    handleSearch() {
      this.initTreeMixins()
        .then(res => {
          // 
          if (Object.keys(this.query).some(key => this.query[key]) && this.treeDataMixins.length) {
            if (this.treeDataMixins[0].children && this.treeDataMixins[0].children.length) {
              const setNodeData = this.treeDataMixins[0].children[0]
              this.defaultExpandKeysMixins = [setNodeData.id]
              this.defaultCheckedNodeMixins = setNodeData.id
              this.setCurrentNode({ ...setNodeData })
            }
          }
        })
    },
    // 
    handleDelete(params, item) {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        console.log({
          id: this.nodeInfo.wmsId
        })
        getDeleteApi({
          id: this.nodeInfo.wmsId
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '',
              message: ``
            })
            this.resetCurrentNode()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },

    // 
    handleGoMaterialSupplier() {
      this.$router.push({
        name: 'InStorageMaterialSupplier',
        params: {

        }
      })
    },
    // 
    downloadExcel() {
      window.open(`${process.env.VUE_APP_BASE_API_WMS}/enterWarehouse/wmsContract/downloadOrderTemplate`)
    },
    // 
    uploadFileSuccess() {
      this.$notify.success({
        title: '',
        message: ``
      })
      this.initTreeMixins()
      this.$refs.buttonList.$refs.uploadFile.$refs.upload.clearFiles()
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // 
    getContractDetail() {
      return new Promise((resolve, reject) => {
        getContractDetailApi({ id: this.nodeInfo.wmsId })
          .then(res => {
            const { code, data } = res
            if (!code) {
              console.log('data: ', data)
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    handleModify() {
      this.getContractDetail()
        .then((res) => {
          this.dialogForm = { ...res.data }
          this.editType = 'modify'
          this.dialogVisible = true
        })
    },
    // 
    handleNodeClick(data, checked, node) {
      this.nodeInfo = { ...data }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.defaultExpandKeysMixins = []
      this.defaultCheckedNodeMixins = ''
      this.initTreeMixins()
        .then(res => {
        // 
          if (this.treeDataMixins.length) {
            if (this.treeDataMixins[0].children && this.treeDataMixins[0].children.length) {
              const setNodeData = this.treeDataMixins[0].children[0]
              this.defaultExpandKeysMixins = [setNodeData.id]
              this.defaultCheckedNodeMixins = setNodeData.id
              this.setCurrentNode({ ...setNodeData })
            }
          }
        })
    },
    handleToggleLoading() {
      this.uploadLoading = !this.uploadLoading
    },
    // 
    beforeUpload() {
      this.uploadLoading = true
    },
    // 
    handleUploadSuccess(res, file, fileList) {
      this.uploadLoading = false
      const { code, data, msg } = res
      if (!code) {
        this.$notify.success({
          title: '',
          message: data || ``
        })
        this.initTreeMixins()
      } else {
        if (code && code === 999) {
          this.$notify.error({
            title: '',
            message: msg || ''
          })
          return
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
::v-deep .el-upload-list__item.is-success.focusing .el-icon-close-tip {
  display: none !important;
}
</style>
