import { getUploadedFileListCommonApi } from '@/apis/modules/common'
import FormUploadFile from '@/components/Upload/FormUploadFile'

export const uploadFilesMixins = {
  data() {
    return {
      uploadFileList: []// 
    }
  },
  methods: {
    //   
    uploadFileSuccess(file) {
      console.log('===================>', file)
      this.uploadFileList.push(file)
    },
    //   xxxx
    uploadListChange(files) {
      this.uploadFileList = files
    },
    // 
    uploadFileDelete(file) {
      this.uploadFileList = this.uploadFileList.filter(item => item.id !== file.id)
    },
    // 
    getFileListByIds(fileIds) {
      return new Promise((resolve, reject) => {
        const ids = fileIds.split(',')
        getUploadedFileListCommonApi({
          ids
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log(data)
            this.uploadFileList = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 
    uploadFileChangeLevel(file) {
      this.uploadFileList = this.uploadFileList.map(item => {
        if (item.id === file.id) {
          item = file
        }
        return item
      })
    }
  }
}

export const formUploadFilesMixins = {
  components: {
    FormUploadFile
  },
  data() {
    return {
      uploadTip: '',
      uploadFileList: [] // 
    }
  },
  methods: {
    //   
    uploadFileSuccess(file) {
      this.uploadFileList.push(file)
      console.log(this.uploadFileList, 'success')
    },
    // 
    handleRemove(removeFileInfo) {
      const { id } = removeFileInfo
      this.uploadFileList = this.uploadFileList.filter(item => item.id !== id)
    },
    // 
    initUploadFileList(fileIdList) {
      if (!fileIdList || fileIdList === 'null') return
      return new Promise((resolve, reject) => {
        getUploadedFileListCommonApi({
          ids: fileIdList
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.uploadFileList = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // id
    getFileIdList() {
      return this.uploadFileList.map(item => item.id)
    },
    // 
    resetFormUploadFilesMixins() {
      this.uploadFileList = []
      console.log(this.uploadFileList, 'reset')
    },
    // 
    downloadUploadFileMixins(fileId) {
      window.open(`${process.env.VUE_APP_BASE_API_LMS}/wms/file/download/${fileId}`)
    }
  }
}
