<template>
  <div>
    <!--  -->
    <el-row class="clearfix">
      <div class="fl">
        <el-button class="filter-item" type="success" :disabled="noDelivery()" @click="handleViewDeliveryOrder"></el-button>
        <el-button class="filter-item" type="primary" :disabled="noDelivery() || node.code === 'ZHY2024OD'" @click="handleAddDeliveryOrder"></el-button>
        <el-button class="filter-item" type="success" :disabled="noDelivery()" icon="el-icon-download" @click="downloadTemplate"></el-button>
        <el-upload
          ref="upload"
          style="display: inline-block;margin: 0 10px;"
          :action="`${BASE_API}/enterWarehouse/wmsDeliveryReceipt/importDeliveryReceipt`"
          :headers="headers"
          :data="{orderCode: node.code}"
          accept=".xls,.xlsx"
          :show-file-list="false"
          :on-success="handleUploadSuccess"
          :before-upload="beforeUpload"
        >
          <el-button
            icon="el-icon-upload"
            type="primary"
            :disabled="noDelivery() || node.code === 'ZHY2024OD'"
          ></el-button>
        </el-upload>
        <!-- :disabled="!+node.id" -->
        <el-button class="filter-item" type="primary" :disabled="!+node.id || node.parentId === 0 || node.code === 'ZHY2024OD'" @click="toDocument"></el-button>
      </div>
      <div class="fr">
        <el-button class="filter-item" type="warning" :disabled="!+node.id || node.parentId === 0 || node.code === 'ZHY2024OD'" @click="handleRemarkModify"></el-button>
        <el-button class="filter-item" type="primary" :disabled="!+node.id || node.parentId !== 0 || node.code === 'ZHY2024'" @click="handleAdd"></el-button>
        <!-- ZHY2024OD -->
        <el-button class="filter-item" type="warning" :disabled="!+node.id || node.parentId === 0 || node.code === 'ZHY2024OD'" @click="handleModify"></el-button>
        <el-button class="filter-item" type="danger" :disabled="!+node.id || node.parentId === 0 || node.code === 'ZHY2024OD'" @click="handleDelete"></el-button>
        <el-button class="filter-item" type="primary" :disabled="!+node.id || node.parentId === 0" @click="handleExport"></el-button>
        <el-button class="filter-item" type="primary" :disabled="!+node.id || node.parentId === 0" @click="handlePrintOrder"></el-button>
      </div>
    </el-row>
    <transition name="fade-transform" mode="out-in">
      <el-empty v-if="!node.id || node.parentId === 0" :description="``" />
      <!--  -->
      <el-row v-else>
        <!--  -->
        <el-row>
          <el-descriptions :column="5" :label-style="{fontWeight: 900}" :content-style="{paddingRight: '10px'}">
            <el-descriptions-item label="">{{ form.supplierName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ form.purchaseDate || '---' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ form.materialsUnit || '---' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ form.orderAmount || '---' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ form.contractName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ form.currency || '---' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ form.exchangeRate || '---' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ form.remarks || '---' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ materialNum || 0 }}</el-descriptions-item>
            <el-descriptions-item label="">{{ form.orderNumber || '---' }}</el-descriptions-item>
          </el-descriptions>
        </el-row>
        <!--  -->
        <el-row>
          <right-list :key="rightListComKey" :node="Object.assign({}, node, {type: form.type})" :info="form" @getMaterialNum="getMaterialNum" />
        </el-row>
      </el-row>
    </transition>

    <!--  -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      :node="node"
      :order-type="4"
      v-on="$listeners"
    />

    <!--  -->
    <el-dialog
      :title="''"
      :visible.sync="remarkVisible"
      width="30%"
      :before-close="beforeClose"
      :lock-scroll="false"
      :destroy-on-close="true"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      append-to-body
    >
      <el-form ref="remarkForm" :model="remarkForm" :rules="remarkRules" label-width="100px">
        <el-form-item label="" prop="remarks">
          <el-input v-model="remarkForm.remarks" type="textarea" :rows="5" />
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button :loading="remarkLoading" type="primary" @click="remarkSave('remarkForm')"> </el-button>
        <el-button @click="beforeClose"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import RightList from './RightList'
import EditDialog from './ContractDialog'
import { getTreeNodeDetailApi, getOrderApi as getDeleteApi, modifyOrderRemarkApi, exportOrderMaterialDetailApi, printOrderDetailApi } from '@/apis/modules/in-storage/purchase'
import $store from '@/store'

const INIT_FORM = {
  supplierName: '',
  purchaseDate: '',
  materialsUnit: '',
  orderAmount: ''
}

export default {
  // name: 'InStoragePurchaseRight',
  components: {
    RightList,
    EditDialog
  },
  mixins: [paginationPage],
  props: {
    node: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    const token = `${$store.getters.token}`
    return {
      title: this.$route.meta.title,
      loading: false,
      remarkLoading: false,
      form: { ...INIT_FORM },
      dialogVisible: false,
      dialogForm: {},
      editType: 'add',
      tableData: [],
      formThead: [
        { label: '', prop: 'materialCode' },
        { label: '', prop: 'materialName' },
        { label: '', prop: 'materialSpec' },
        { label: '', prop: 'orderNum' },
        { label: '', prop: 'price' },
        { label: '', prop: 'unitMaterial' },
        { label: '', prop: 'dataSources_dictText' },
        { label: '', prop: 'dataSources_dictText1' }
      ],
      materialNum: 0,
      remarkVisible: false,
      remarkForm: {
        remarks: ''
      },
      remarkRules: {
        remarks: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      rightListComKey: 0,
      BASE_API: process.env.VUE_APP_BASE_API_WMS,
      headers: { 'Authorization': token },
      fullscreenLoading: null
    }
  },
  computed: {
    dialogTitle() {
      return this.editType === 'add' ? `${this.title}` : `${this.title}`
    }
  },
  watch: {
    'node.id': {
      handler: function(val) {
        if (val && this.node.parentId !== 0) {
          this.init()
          this.rightListComKey++
        }
      },
      immediate: true
    }
  },
  methods: {
    init() {
      return new Promise((resolve, reject) => {
        const params = {
          id: this.node.wmsId
        }
        this.loading = true
        getTreeNodeDetailApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            const { wmsOrderMaterialJoinList, ...info } = data
            this.form = { ...info }
            this.remarkForm.remarks = this.form.remarks || ''
            this.tableData = wmsOrderMaterialJoinList
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    //  id
    handleViewDeliveryOrder() {
      this.$router.push({
        // type2-1-
        name: 'InStorageFinishDelivery',
        params: {
          id: this.form.id, // id
          // Tip type14
          type: 4 // 1234
        }
      })
    },
    // 
    handleAddDeliveryOrder() {
      this.$router.push({
        name: 'InStorageDeliveryAdd',
        params: {
          view: 'add',
          orderId: this.form.id, // id
          // Tip type14
          type: 4, // 1234
          routeName: this.$route.name
        }
      })
    },
    // 
    toDocument() {
      this.$router.push({
        name: 'InStoragePurchaseDocument',
        params: {
          id: this.form.id // id
        }
      })
    },
    handleSubmit() {
      this.init()
      this.$nextTick(() => {
        this.$emit('refreshTree')
      })
    },
    async handleDelete() {
      this.$confirm(`${this.title}, ?`, '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        if (!this.node.wmsId) return
        getDeleteApi({
          id: this.node.wmsId
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '',
              type: 'success'
            })
            this.form = { ...INIT_FORM }
            this.$emit('resetCurrentNode')
          }
        })
      }).catch(() => {
        this.$message({
          message: '',
          type: 'info'
        })
      })
    },
    noDelivery() {
      if (!+this.node.id || this.node.parentId === 0 || !this.node.children) {
        return true
      } else if (!this.node.children) {
        return true
      } else {
        return false
      }
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // 
    handleRemarkModify() {
      this.remarkVisible = true
    },
    // 
    handleModify() {
      this.dialogForm = { ...this.form, wmsOrderMaterialJoinList: this.tableData }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    // 
    handlePrintOrder() {
      return new Promise((resolve, reject) => {
        printOrderDetailApi({ id: this.form.id /** id */ })
          .then(res => {
            const { code, data } = res
            if (!code) {
              window.open(`${process.env.VUE_APP_BASE_API_WMS}${data}`)
            }
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            reject(err)
          })
      })
    },
    handleExport() {
      this.$confirm('?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      })
        .then(() => {
          exportOrderMaterialDetailApi({
            id: this.form.id // id
          })
            .then(res => {
              this.download(res, '.xlsx')
            })
            .catch(e => {
              console.log(e)
              this.$notify.error({
                title: '',
                message: ``
              })
            })
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    download(result, name) {
      const filename = name
      const blob = new Blob([result.data], { type: 'application/vnd.ms-excel' })
      const url = window.URL.createObjectURL(blob)
      if (window.navigator.msSaveBlob) { // IE
        try {
          window.navigator.msSaveBlob(blob, filename)
        } catch (e) {
          console.log(e)
        }
      } else { // IE
        const link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', filename)
        document.body.appendChild(link)
        link.click()
      }
      URL.revokeObjectURL(url) // 
    },
    // (tabPanel)
    getMaterialNum(val) {
      this.materialNum = val
    },
    // 
    beforeClose() {
      this.remarkVisible = false
    },
    // 
    remarkSave(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // loading
          this.remarkLoading = true
          modifyOrderRemarkApi({ id: this.form.id, ...this.remarkForm })
            .then(res => {
              this.remarkLoading = false
              // 
              this.init()
            })
            .catch(err => {
              this.remarkLoading = false
              console.log('err', err)
            })
          // 
          this.remarkVisible = false
          // 
          this.resetForm(formName)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    // 
    beforeUpload() {
      this.$emit('toggleLoading')
    },
    // 
    handleUploadSuccess(res, file, fileList) {
      const { code, data, msg } = res
      setTimeout(() => {
        this.$emit('toggleLoading')
        if (!code) {
          this.$notify.success({
            title: '',
            message: ``
          })
          this.$emit('refreshTree')
          // { "allStockIn": false, "children": [], "code": "4001312689", "createBy": null, "createDate": null, "delFlag": null, "factorySign": "", "id": 1, "orderBy": "", "parentId": -1, "supplierId": 127, "supplierName": "300005188 ", "updateBy": null, "updateDate": null, "wmsId": 1 }
          // Tip 
          // InStoragePurchase // 
          // InStorageFinish // 
          const obj = {
            InStoragePurchase: 1,
            InStorageFinish: 4
          }
          this.$router.push({
            name: 'InStorageDeliveryModify',
            params: {
              view: 'modify',
              orderId: this.node.wmsId,
              id: data,
              type: obj[this.$route.name],
              routeName: this.$route.name
            }
          })
        } else {
          if (code && code === 999) {
            this.$notify.error({
              title: '',
              message: msg || ''
            })
            return
          }
        }
      }, 500)
    },
    downloadTemplate() {
      window.open(`${process.env.VUE_APP_BASE_API_WMS}/enterWarehouse/wmsDeliveryReceipt/downloadImportTemplate
`)
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
