<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="" prop="code">
          <el-input v-model.trim="form.code" :disabled="editType !== 'add'" placeholder="" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="" prop="supplierId">
          <template>
            <div class="selected-wrapper">
              <el-input v-model.trim="form.supplierName" disabled placeholder="" style="width: 143px;" />
              <el-button plain type="primary" style="margin-left:5px;padding-left:0;padding-right:0;width:30px" icon="el-icon-search" @click="selectInfo.visible = true" />
            </div>
          </template>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model.trim="form.name" placeholder="" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model.trim="form.type" filterable clearable placeholder="" style="width: 178px;">
            <el-option label="" value="1" />
            <el-option label="" value="3" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="currency">
          <el-input v-model.trim="form.currency" placeholder="" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="" prop="exchangeRate">
          <el-input v-model.trim="form.exchangeRate" placeholder="" style="width: 178px;" />
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? ' ...' : ' ' }}</el-button>
        <el-button @click="beforeClose"> </el-button>
      </div>
    </el-form>

    <!--   -->
    <model-select
      v-model="selectInfo.visible"
      :multiple="false"
      @confirm="onSelectConfirm"
    />

  </el-drawer>
</template>

<script>
import ModelSelect from '@/components/ModalSelect/Supplier'
import { addTreeListApi as addApi, editTreeListApi as updateApi } from '@/apis/modules/in-storage/purchase'

const INIT_FORM = () => ({
  code: '',
  supplierId: '',
  supplierName: '',
  name: '', // 
  type: '', // 
  currency: '', // 
  exchangeRate: '' // 
})

export default {
  name: 'InStoragePurchaseTreeAddCom',
  components: {
    ModelSelect
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      loading: false,
      rules: {
        code: [
          { required: true, message: '', trigger: 'blur' }
        ],
        supplierId: [
          { required: true, message: '', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '', trigger: 'change' }
        ],
        currency: [
          { required: true, message: '', trigger: 'blur' }
        ]
        // exchangeRate: [
        //   { required: true, message: '', trigger: 'blur' }
        // ]
      },
      selectInfo: {
        list: [],
        visible: false,
        radio: ''
      }
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          const params = {
            ...this.form
          }
          const api = {
            add: addApi,
            modify: updateApi
          }[this.editType]
          api(params)
            .then(res => {
              const { code } = res
              if (!code) {
                this.$message({
                  message: `${this.title}`,
                  type: 'success'
                })
                this.$emit('handleRefreshTree')
                this.beforeClose()
              }
              this.loading = false
            })
            .catch(err => {
              console.log(err)
              this.loading = false
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    onSelectConfirm(list) {
      if (list.length) {
        this.form.supplierId = list[0].id
        this.form.supplierName = list[0].name
      }
    },
    initSelect() {
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM() })
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep:focus {
    outline: 0;
  }
  .scroll-drawer {
    height: calc(100vh - 150px);
    // overflow: auto;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }
  ::v-deep .el-drawer {
    width: 640px;
  }
  .drawer-footer{
    text-align: right;
  }
  </style>

