<template>
  <div>
    <el-tabs v-model="viewType" type="card" class="main-tab">
      <el-tab-pane label="" name="All" />
      <el-tab-pane label="" name="UnReceive" />
      <el-tab-pane label="" name="Inspect" />
      <el-tab-pane label="" name="UnInStorage" />
      <el-tab-pane label="" name="InStorage" />
      <el-tab-pane label="" name="PurchaseOrderHistory" />
    </el-tabs>

    <div v-if="viewType !== 'PurchaseOrderHistory'" class="top-filter clearfix">
      <el-input
        v-model="nameOrCode"
        clearable
        class="filter-item"
        placeholder=""
        style="width: 178px;"
      />
      <el-button
        class="filter-item"
        type="success"
        icon="el-icon-search"
        @click="handleSearch"
      ></el-button>
    </div>

    <transition name="fade-transform" mode="out-in">
      <!--  -->
      <div v-if="viewType === 'All'" key="All">
        <All ref="All" v-bind="Object.assign($attrs, {nameOrCode})" v-on="$listeners" />
      </div>
      <!--  -->
      <div v-if="viewType === 'UnReceive'" key="UnReceive">
        <UnReceive ref="UnReceive" v-bind="Object.assign($attrs, {nameOrCode})" v-on="$listeners" />
      </div>
      <!--  -->
      <div v-if="viewType === 'Inspect'" key="Inspect">
        <Inspect ref="Inspect" v-bind="Object.assign($attrs, {nameOrCode})" v-on="$listeners" />
      </div>
      <!--  -->
      <div v-if="viewType === 'UnInStorage'" key="UnInStorage">
        <UnInStorage ref="UnInStorage" v-bind="Object.assign($attrs, {nameOrCode})" v-on="$listeners" />
      </div>
      <!--  -->
      <div v-if="viewType === 'InStorage'" key="InStorage">
        <InStorage ref="InStorage" v-bind="Object.assign($attrs, {nameOrCode})" v-on="$listeners" />
      </div>
      <!--  -->
      <div v-if="viewType === 'PurchaseOrderHistory'" key="PurchaseOrderHistory">
        <PurchaseOrderHistory ref="PurchaseOrderHistory" v-bind="Object.assign($attrs, {nameOrCode})" v-on="$listeners" />
      </div>
    </transition>

  </div>
</template>

<script>
import All from './All'
import UnReceive from './UnReceive'
import Inspect from './Inspect'
import InStorage from './InStorage'
import UnInStorage from './UnInStorage'
import PurchaseOrderHistory from './PurchaseOrderHistory'

export default {
  name: 'InStoragePurchaseRightList',
  components: {
    All,
    UnReceive,
    Inspect,
    InStorage,
    UnInStorage,
    PurchaseOrderHistory
  },
  data() {
    return {
      viewType: 'All',
      nameOrCode: ''
    }
  },
  methods: {
    handleSearch() {
      this.$refs[this.viewType].init()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
